
import React, { useState, useContext } from 'react';
import { AppContext } from '../../AppContext';
import './checkout.css'
import {
    Accordion,
    AccordionBody,
    AccordionHeader,
    AccordionItem,
    Button,
    Col,
    Container,
    Form,
    FormGroup,
    Input,
    Label,
    Row,
    Table,
} from 'reactstrap';
const Checkout = () => {
    const { cart, cart2, cart3, remove, remove2, remove3, updateQty, updateQty2, updateQty3 } = useContext(AppContext)
    const [open, setOpen] = useState('1');
    const toggle = (id) => {
        if (open === id) {
            setOpen();
        } else {
            setOpen(id);
        }
    };
    return (
        <div>
            <Container>
                <div>
                    <h1 className='tt'>Thng tin t hng</h1>
                </div>
                <div className='checkout'>
                    <div className='adre'>
                        <Form>
                            <Row>
                                <Col md={6}>
                                    <FormGroup>
                                        <Label for="name">
                                            H v Tn
                                        </Label>
                                        <Input
                                            id="name"
                                            name="name"
                                            placeholder="Nguyn Vn A"
                                            type="Text"
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={4}>
                                    <FormGroup>
                                        <Label for="name">
                                            S in thoi
                                        </Label>
                                        <Input
                                            id="name"
                                            name="name"
                                            placeholder="S in thoi"
                                            type="Text"
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={8}>
                                    <FormGroup>
                                        <Label for="exampleAddress2">
                                            a ch nhn hng
                                        </Label>
                                        <Input
                                            id="exampleAddress2"
                                            name="address2"
                                            placeholder="123 Trn Hng o"
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>

                            <Row>
                                <Col md={3}>
                                    <FormGroup>
                                        <Label for="exampleCity">
                                            Tnh/Thnh Ph
                                        </Label>
                                        <Input
                                            id="exampleCity"
                                            name="city"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md={3}>
                                    <FormGroup>
                                        <Label for="exampleState">
                                            Qun/Huyn
                                        </Label>
                                        <Input
                                            id="exampleState"
                                            name="state"
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={8}>
                                    <FormGroup>
                                        <Label for="exampleCity">
                                            Yu cu c bit
                                        </Label>
                                        <Input
                                            id="exampleCity"
                                            name="city"
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Label for="payment">
                                Hnh thc thanh ton
                            </Label>
                            <Row>
                                <Col md={6}>
                                    <FormGroup check>
                                        <Input name="radio1" type="radio" />
                                        <Accordion open={open} toggle={toggle}>
                                            <AccordionItem>
                                                <AccordionHeader targetId="1" className='the'>Thanh ton bng th</AccordionHeader>
                                                <AccordionBody accordionId="1">
                                                    <Row>
                                                        <Col md={9}>
                                                            <FormGroup>
                                                                <Label for="cardnumber">
                                                                    S th
                                                                </Label>
                                                                <Input
                                                                    id="cardnumber"
                                                                    name="cardnumber"
                                                                />
                                                            </FormGroup>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md={6}>
                                                            <FormGroup>
                                                                <Label for="exampleCity">
                                                                    Ngy ht hn
                                                                </Label>
                                                                <Input
                                                                    id="exampleCity"
                                                                    name="city"
                                                                />
                                                            </FormGroup>
                                                        </Col>
                                                        <Col md={4}>
                                                            <FormGroup>
                                                                <Label for="exampleState">
                                                                    CVV
                                                                </Label>
                                                                <Input
                                                                    id="exampleState"
                                                                    name="state"
                                                                />
                                                            </FormGroup>
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col md={12}>
                                                            <FormGroup>
                                                                <Label for="exampleCity">
                                                                    Tn ch th
                                                                </Label>
                                                                <Input
                                                                    id="exampleCity"
                                                                    name="city"
                                                                />
                                                            </FormGroup>
                                                        </Col>
                                                    </Row>
                                                </AccordionBody>
                                            </AccordionItem>
                                        </Accordion>
                                    </FormGroup>
                                </Col>
                            </Row>

                            <FormGroup check>
                                <Input name="radio1" type="radio" />

                                <AccordionHeader className='shipcode'>Thanh ton khi nhn hng</AccordionHeader>
                            </FormGroup>
                            <div className='btncheckout'>
                                <button>
                                    t hng
                                </button>
                            </div>

                        </Form>
                    </div>
                    <div className='cartinf'>
                        <div className='cartinf1'>
                            <Table
                            >
                                <tbody className='bodycart1'>
                                    {
                                        cart2.map((item, index) => (
                                            (
                                                <tr>
                                                    <th scope="row">
                                                        <img className='imgcart' src={item.img}></img>
                                                    </th>
                                                    <td>
                                                        {item.name}
                                                    </td>
                                                    <td className='price'>
                                                        {item.price}
                                                    </td>
                                                    <td className='quan'>
                                                        {/* <button className='-' onClick={() => updateQty2(item.id, -1)}>-</button> */}
                                                        SL: {item.qty}
                                                        {/* <button className='+' onClick={() => updateQty2(item.id, 1)}>+</button> */}
                                                    </td>
                                                    <td>
                                                        <span onClick={() => remove2(item.id)}><i class="fa-solid fa-trash bin"></i></span>
                                                    </td>
                                                </tr>
                                            )
                                        ))

                                    }
                                    {
                                        cart.map((item, index) => (
                                            (
                                                <tr>
                                                    <th scope="row">
                                                        <img className='imgcart' src={item.img}></img>
                                                    </th>
                                                    <td>
                                                        {item.name}
                                                    </td>
                                                    <td className='price'>
                                                        {item.price}
                                                    </td>
                                                    <td className='quan'>
                                                        {/* <button  onClick={() => updateQty(item.id, -1)}>-</button> */}
                                                        SL: {item.qty}
                                                        {/* <button onClick={() => updateQty(item.id, 1)}>+</button> */}
                                                    </td>
                                                    <td>
                                                        <span onClick={() => remove(item.id)}><i class="fa-solid fa-trash bin"></i></span>
                                                    </td>
                                                </tr>
                                            )
                                        ))

                                    }
                                    {
                                        cart3.map((item, index) => (
                                            (
                                                <tr>
                                                    <th scope="row">
                                                        <img className='imgcart' src={item.img}></img>
                                                    </th>
                                                    <td>
                                                        {item.name}
                                                    </td>
                                                    <td className='price'>
                                                        {item.price}
                                                    </td>
                                                    <td className='quan'>
                                                        {/* <button className='-' onClick={() => updateQty3(item.id, -1)}>-</button> */}
                                                        SL: {item.qty}
                                                        {/* <button className='+' onClick={() => updateQty3(item.id, 1)}>+</button> */}
                                                    </td>
                                                    <td>
                                                        <span onClick={() => remove3(item.id)}><i class="fa-solid fa-trash bin"></i></span>
                                                    </td>
                                                </tr>
                                            )
                                        ))

                                    }
                                </tbody>
                            </Table>
                            <p>Ph ship: 15000 VN</p>
                        <div className='tong'>
                            <h3 >Tng: {cart2.reduce((tong, item) => item.qty * item.price + tong, 0) + cart.reduce((tong, item) => item.qty * item.price + tong, 0) + cart3.reduce((tong, item) => item.qty * item.price + tong, 0) + 15000} VN</h3>
                        </div>
                        </div>
                        <div className='btncheckout1'>
                                <button>
                                    t hng
                                </button>
                            </div>


                    </div>
                </div>
            </Container>

        </div>
    );
}

export default Checkout;
