import React, { useEffect, useState, useContext } from 'react';
import axios from 'axios';
import { Link } from "react-router-dom";
import { AppContext } from '../../AppContext';
import './product.css'
import { Form, Button, Card, CardBody, CardText, CardTitle, Col, Container, Row, Modal, ModalHeader, ModalBody, ModalFoote, ModalFooter, FormGroup, Label, Input } from 'reactstrap';
import food from '../../images/catfood8.jpg'
import { useForm } from 'react-hook-form';
const Product = () => {
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);
    const { register, handleSubmit } = useForm();
    const url = 'https://66124e8c95fdb62f24ee62fe.mockapi.io/product'
    const [data, setData] = useState(null)
    const getData = () => {
        axios.get(url).then(res => {
            setData(res.data)
        }).catch((error) => {
            console.log(error)
        })

    }
    useEffect(() => {
        getData()
    }, [])
    const { addCart, onSearch } = useContext(AppContext)
    return (
        <div>
            <div className='header_img'>
                <img className='banner2' src={food} alt='catfood' />
            </div>
            <div className='header_product' data-aos="fade-right">
                <h3>Thc Phm Cho Mo ca Moon Shop</h3>
                <p>Hy kt hp c thc n kh v thc n t  duy tr sc kho tt cho Boss ca bn!!!</p>
                <Button className='TV' onClick={toggle}>Cn t vn</Button>
                <Modal isOpen={modal} toggle={toggle}>
                    <ModalHeader toggle={toggle}>in thng tin lin lc</ModalHeader>
                    <ModalBody>
                        <Form>
                            <FormGroup>
                                <Label
                                    for="exampleEmail"
                                    hidden
                                >
                                    H v Tn
                                </Label>
                                <Input
                                    id="exampleEmail"
                                    name="H v tn"
                                    placeholder="H v tn"
                                    type="email"
                                />
                            </FormGroup>
                            {' '}
                            <FormGroup>
                                <Label
                                    for="phone"
                                    hidden
                                >
                                    S in thoi
                                </Label>
                                <Input
                                    id="phone"
                                    name="S in thoi"
                                    placeholder="S in thoi"
                                    type="text"
                                />
                            </FormGroup>
                            {' '}
                            <FormGroup>
                                <Label
                                    for="Comment"
                                    hidden
                                >
                                    Vn  cn h tr
                                </Label>
                                <Input className='noidung'
                                    id="comment"
                                    name="comment"
                                    placeholder="Ni dung cn h tr"
                                    type="textarea"
                                />
                            </FormGroup>
                        </Form>
                    </ModalBody>
                    <ModalFooter>
                        <Button className='gui' onClick={toggle}>
                            Gi
                        </Button>{' '}
                        <Button color="secondary" onClick={toggle}>
                            Hu
                        </Button>
                    </ModalFooter>
                </Modal>
            </div>

            <Container>

                <div className='find'>
                    <div>
                        <Form className='name3' onSubmit={handleSubmit(onSearch)}>
                            <FormGroup>
                                <Label
                                    for="text"
                                    hidden
                                >
                                    Tn sn phm
                                </Label>
                                <Input {...register("name")}
                                    className='name2'
                                    // name="name"
                                    placeholder="Tm sn phm"
                                    type="text"
                                />
                            </FormGroup>
                        </Form>
                    </div>
                    <div>
                        <button className='tim' type='submit'>
                            tm
                        </button>
                    </div>
                </div>
                <Row>
                    {
                        data && data.map((item, index) => (
                            <Col md={3}>
                                <Card className='card'>
                                    <Link className='name cardtext' to={`/product/${item.id}`} key={index}>

                                        <img className='img'
                                            src={item.img}
                                        />
                                        <CardBody className='cardtext'>
                                            <CardTitle tag="h4">
                                                <div>{item.name}</div>
                                            </CardTitle>

                                            <CardText>
                                                <span className='sale'>{item.sale}</span><span> - </span><span className='price'> {item.price}<span></span></span>
                                            </CardText>

                                        </CardBody>
                                    </Link>
                                    <div className='buy2'>
                                                <Button className='buy3' onClick={() => addCart(item.id)}>
                                                    Thm vo gi hng
                                                </Button>
                                                <Link className='name cardtext' to={`/product/${item.id}`} key={index}>
                                                    <Button className='buy1'>Chi tit</Button>
                                                </Link>
                                            </div>
                                </Card>
                            </Col>
                        ))
                    }
                </Row>
            </Container>

        </div>
    );
}

export default Product;
