import React from 'react';
import './banner.css'
import 'react-slideshow-image/dist/styles.css'
import Img2 from '../../images/catfood1.jpg'
import Img3 from '../../images/catfood4.jpg'
import { Slide } from 'react-slideshow-image';
import {
    Carousel,
    Collapse,
    Container,
    Fade,
} from 'reactstrap';
const proprietes = {
    duration: 5000,
    transitionDuration: 500,
    infinite: true,
    indicators: true,
    arrows: true
}
const Banner = () => {

    return (
        <div className="containerSlide">
            <Slide {...proprietes}>
                <div className="each-slide">
                    <div className='banner'>
                        <div className='hinh'> 
                            <img className='hat' src={Img2} alt="img1" />
                            </div>
                        <Container>
                        <span className='contain' data-aos="fade-down">
                            <h1>Gii Php ti u dinh dng BOSS</h1>
                            <h1>Tit kim thi gian cho SEN</h1>
                            <p>Cung cp cho mo mt ch  dinh dng ton din v y . m bo hng chnh hng.</p>
                        </span>
                        </Container>

                    </div>
                </div>
                <div className="each-slide">
                    <div className='banner'>
                        <div className='hinh'>
                        <img className='hat' src={Img3} alt="img2" />
                        </div>
                        <Container>
                        <span className='contain white'>
                            <h1>Hy to mi trng sng lnh mnh cho BOSS</h1>
                            <h1> c th bn cnh BOSS ca bn lu hn</h1>
                            <p>Cung cp cho mo nhng sn phm dnh dng b sung v ph kin cht lng gp phn to nn mi trng sng hon ho cho mo. </p>
                        </span>
                        </Container>

                    </div>
                </div>
            </Slide>
        </div>
    );
}

export default Banner;
