import React, { useEffect, useState, useContext } from 'react';
import axios from 'axios';
import {  Link } from "react-router-dom";
import { AppContext } from '../../AppContext';
import './product.css'
import toy from '../../images/toy.jpg'
import {Form, Button, Card, CardBody, CardSubtitle, CardText, CardTitle, Col, Container, Modal, ModalBody, ModalHeader, Row, FormGroup, Label, Input, ModalFooter } from 'reactstrap';
const Product3 = () => {
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);
    const url = 'https://6649acdb4032b1331bee85d7.mockapi.io/product3'
    const [data, setData] = useState(null)
    const getData = () => {
        axios.get(url).then(res => {
            setData(res.data)
        }).catch((error) => {
            console.log(error)
        })

    }
    useEffect(() => {
        getData()
    }, [])
    const { addCart3 } = useContext(AppContext)
    return (
        <div>
            <div className='bannerimg'>
            <img className='banner3' src={toy} alt='toy of cat' />
            </div>
            
            <div className='header_product' data-aos="fade-right">
                <h3 className='contain4'>Ph kin Ca Moon Shop</h3>
                <p className='contain5'>Hy to cho Boss s pht trin lnh mnh v hnh phc nht, bng cch xy dng mt mi trng tin nghi v ng yu bng nhng mn ph kin nh nh!!!</p>
                <Button className='TV' onClick={toggle}>Cn t vn</Button>
                <Modal isOpen={modal} toggle={toggle}>
                    <ModalHeader toggle={toggle}>in thng tin lin lc</ModalHeader>
                    <ModalBody>
                        <Form>
                            <FormGroup>
                                <Label
                                    for="exampleEmail"
                                    hidden
                                >
                                    H v Tn
                                </Label>
                                <Input
                                    id="exampleEmail"
                                    name="H v tn"
                                    placeholder="H v tn"
                                    type="email"
                                />
                            </FormGroup>
                            {' '}
                            <FormGroup>
                                <Label
                                    for="phone"
                                    hidden
                                >
                                    S in thoi
                                </Label>
                                <Input
                                    id="phone"
                                    name="S in thoi"
                                    placeholder="S in thoi"
                                    type="text"
                                />
                            </FormGroup>
                            {' '}
                        </Form>
                    </ModalBody>
                    <ModalFooter>
                        <Button className='gui' onClick={toggle}>
                            Gi
                        </Button>{' '}
                        <Button color="secondary" onClick={toggle}>
                            Hu
                        </Button>
                    </ModalFooter>
                </Modal>
            </div>
            <Container>
                <Row>
                    {
                        data && data.map((item, index) => (
                            <Col>
                                <Card className='card'>
                                    <Link className='name cardtext' to={`/product3/${item.id}`} key={index}>
                                        <img className='img'
                                            src={item.img}
                                        />
                                        <CardBody className='cardtext'>
                                            <CardTitle tag="h4">
                                                <div>{item.name}</div>
                                            </CardTitle>
                                            <CardText>
                                                <span className='sale'>{item.sale}</span><span> - </span><span className='price'> {item.price}<span></span></span>
                                            </CardText>
                                        </CardBody>
                                    </Link>
                                    <div className='buy2'>
                                        <Button className='buy3' onClick={() => addCart3(item.id)}>
                                            Thm vo gi hng
                                        </Button>
                                        <Link className='name cardtext' to={`/product3/${item.id}`} key={index}>
                                            <Button className='buy1'>Chi tit</Button>
                                        </Link>
                                    </div>

                                </Card>
                            </Col>
                        ))
                    }
                </Row>
            </Container>

        </div>
    );
}

export default Product3;
